import React from 'react'
import './projectContainer.scss'

const ProjectContainer = ({alt}) => {
    if (alt === "left") {
        return this.left
    } else {
        return right
    }
    
}

const left = (
    <div>
        Left
    </div>
)

const right = (
    <div>
        right
    </div>
)


export default ProjectContainer